import * as types from '../../mutation-types';

const state = {
    _inner_map: {
        'credit-consume': 'A',
        'credit-withdrawals': 'B',
        'credit-borrow-money': 'L',
        'credit-order-stages': 'Y'
    },
    _Credit_Map: {
        A: { title: '惠消费', no: '239', credit_no: '1001', module: 'credit-consume' },
        B: { title: '惠提现', no: '241', module: 'credit-withdrawals' },
        L: { title: '惠借钱', no: '242', credit_no: '3001', module: 'credit-borrow-money' },
        Y: { title: '惠分单', no: '240', credit_no: '2001', module: 'credit-order-stages' }
    }
};

// getters
const getters = {
    creditMap: state => state._Credit_Map,
    innerMap: state => state._inner_map
};

// actions
const actions = {};

// mutations
const mutations = {};

export default {
    state,
    getters,
    actions,
    mutations
};



