<template>
    <div class="selector-inline selector-action" :class="{ show : currentShow }"   >
        <div class="mask" @click=" close()  "></div>
        <div class="content">
            <div class="hd">
                <a href="javascript:;" class="btn-cancel" @click=" close() ">取消</a>
                <h2>{{ title }} </h2>
            </div>
            <div class="bd">
                <ul class="list">
                    <li class="item" :class="{ checked: item[key] == checkedValue  }" v-for="( item, index ) in actionList " :key=" index ">
                        <a href="javascript:;" @click="change( item, index )">
                            <cite>{{ item[value] }}</cite>
                        </a>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>

import preventPageScroll from 'wc-utils/prevent-page-scroll';

export default {
    props: [
        'options'
    ],
    data() {
        return {
            currentShow: false,
            resolve: '',
            reject: '',
            promise: ''
        };
    },
    created(){
    },
    computed: {
        title () {
            return this.options.title || '请选择';
        },
        checkedValue () {
           return this.options.checked ? this.options.checked : '';
        },
        key () {
            return this.options.key || 'id';
        },
        value () {
            return this.options.value || 'name';
        },
        actionList () {
            console.log(this.options.actionList);
           return this.options.actionList || [];
        }
    },
    components: {
    },
    methods: {
        change (item, index) {
            this.currentShow = false;
            this.resolve({ item, index });
        },
        close () {
            preventPageScroll.recover();
            this.currentShow = false;
            this.reject();
        },
        show () {
            preventPageScroll.prevent();
            this.currentShow = true;
            this.promise = new Promise((resolve, reject) => {
                this.resolve = resolve;
                this.reject = reject;
            });
            return this.promise;
        }
    }
};
</script>



<style lang="scss" scoped>

    $item-height: 120px;

    .list
    {
        .item
        {
            position: relative;

            @include mini-border(bottom);
            a
            {
                display: block;
                position: relative;
                padding: 0px 0px 0px 50px;
                min-height: $item-height;
                cite
                {
                    display: block;
                    padding-bottom: 8px;
                    color: #272727;
                    font-size: 16px;
                    line-height: $item-height;
                }
            }
        }
        .item:last-child
        {
            &:before
            {
                content: none;
            }
        }
        .item.checked
        {
            a
            {
                &:after
                {
                    content: '';
                    position: absolute;
                    top: 50%;
                    right: 50px;

                    width: 40px;
                    height: 40px;
                    
                    @include bg-offset-center();
                    @include transform(translateY(-50%));

                    @include image-set('../../assets/img/bank/selector/check-card');
                }
            }
        }
    }
    
</style>



