const state = {
    search: {
        accountList: [],
        accountInfo: {
            PAYER_ACCOUNT: '', // 操作账号
            AMOUNT: '', // 销户金额
            PAYEE_ACCOUNT: '', // 绑定账号
            ACCTSOURCE: '', // 账户来源
            BINDACCTTYPE: '', // 绑定账户类型
            PAY_PWD: '' // 支付密码
        }
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateAccountCancellationSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearAccountCancellationSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            accountList: [],
            accountInfo: {
                PAYER_ACCOUNT: '',
                AMOUNT: '',
                PAYEE_ACCOUNT: '',
                ACCTSOURCE: '',
                BINDACCTTYPE: '',
                PAY_PWD: ''
            }
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



