const state = {
    search: {
        list: [],
        payeeName: '',
        payeeAccount: '',
        payeeBankName: '',
        bankName: '',
        account: '',
        payer: '',
        money: '',
        remark: ''
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateLargeAmountOfRemittanceSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearLargeAmountOfRemittanceSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            list: [],
            payeeName: '',
            payeeAccount: '',
            payeeBankName: '',
            bankName: '',
            account: '',
            payer: '',
            money: '',
            remark: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



