const state = {
    search: {
        paymentAccountList: [],
        checked: {},
        patternName: '实时到账',
        TRAN_TYPE: '0', // 转账方式 0-实时 1-次日
        COR_NAME: '', // 收款户名
        PAYEE_ACCOUNT: '', // 收款账号
        PAYER_ACCOUNT: '', // 付款账户
        CURRENCY: '0', // 付款币种 0-人民币
        AMOUNT: '', // 转账金额
        FEE: '', // 手续费
        SENDMENO: '', // 附言
        ISMOBILETRAN: '0', // 是否手机号转账
        PAY_PWD: '', // 交易密码
        MSGCODE: '' // 短信验证码
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateInternalTransferSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearInternalTransferSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            checked: {},
            patternName: '实时到账',
            TRAN_TYPE: '0',
            COR_NAME: '',
            PAYEE_ACCOUNT: '',
            PAYER_ACCOUNT: '',
            CURRENCY: '0',
            AMOUNT: '',
            FEE: '',
            SENDMENO: '',
            ISMOBILETRAN: '',
            PAY_PWD: '',
            MSGCODE: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



