<template>
	<div class="tip-page">
		<div class="tip-page-header">
			<div v-if="tipImg === 'success'" class="tip-page-img img-success"></div>
			<div v-if="tipImg === 'error'" class="tip-page-img img-error"></div>
			<p class="tip-page-title">{{tipTitle}}</p>
			<p class="tip-page-msg">
				<span class="time-color">{{tipTime}}</span>
				<span>{{tipMsg}}</span>
			</p>
		</div>
		<slot>
			
		</slot>
		<div v-if="tipButton" class="tip-page-button cell-action">
			<mt-button @click.native="submit" type="primary" size="large">{{tipButton}}</mt-button>
		</div>
	</div>

</template>

<script>

export default{
	props: {
		tipImg: {
			type: String,
			default: 'success'
		},
		tipTitle: {
			type: String,
			default: ''
		},
		tipMsg: {
			type: String,
			default: ''
		},
		tipTime: {
			type: String,
			default: ''
		},
		tipButton: {
			type: String,
			default: ''
		}

	},
	methods: {
		submit(){
			this.$emit('submit');
		}
	}
};

</script>

<style lang="scss" scoped>
.tip-page{
	.tip-page-header{
		padding: 48px 50px 40px;
		background-color: #fff;
		text-align: center;
		.tip-page-img{
			width: 120px;
			height: 120px;
			margin: auto auto;
			background-size: cover;
		}
		.img-success{
			@include image-set('../assets/img/tip/success');
		}
		.img-error{
			@include image-set('../assets/img/tip/error');
		}
		.tip-page-title{
			margin-top: 40px;
			margin-bottom: 10px;
			height: 48px;
			line-height: 48px;
			font-size: 17px;
			color: #272727;
		}
		.tip-page-msg{
			font-size: 13px;
			color: #B5B5B5;
			.time-color{
				color: #FF511A;
			}
		}
	}
	.tip-page-button{
		margin-top: 80px;
	}
}
</style>


