<template>
    <ul class="icon-list" :class="[ type ]"  >
        <li class="item wechat">
            <a href="javascript:;" @click="share('wechat')">
                微信好友
            </a>
        </li>
        <li class="item friends">
            <a href="javascript:;" @click="share('friends')">
                朋友圈
            </a>
        </li>
        <li class="item qq">
            <a href="javascript:;" @click="share('qq')">
                QQ
            </a>
        </li>
        <li class="item weibo">
            <a href="javascript:;" @click="share('weibo')">
                微博
            </a>
        </li>
    </ul>
</template>

<script>
export default {
    props: {
        type: {
            type: String,
            default: 'inline'
        }
    },
    methods: {
        share (key){
            console.log(key);
            this.$emit('click', key );
        }
    }
};
</script>

<style lang="scss" scoped >

    .icon-list
    {
        @include clearfix();
        .item
        {
            float: left;
            width: 25%;
            a
            {
                position: relative;
                display: block;
                padding-top: 124px;
                height: 200px; 
                text-align: center;
                font-size: 15px;
                color: #272727; 
                &:before
                {
                    content: '';
                    position: absolute;
                    top: 40px;
                    left: 50%;
                    width: 60px;
                    height: 60px;
                    @include transform(translate(-50%));
                }
            }
        }
        .item.wechat
        {
            a
            {
                &:before
                {
                    @include image-set('../../assets/img/components/wechat');   
                }
            }
        }
        .item.friends
        {
            a
            {
                &:before
                {
                    @include image-set('../../assets/img/components/friends');   
                }
            }
        }
        .item.qq
        {
            a
            {
                &:before
                {
                    @include image-set('../../assets/img/components/qq');   
                }
            }
        }
        .item.weibo
        {
            a
            {
                &:before
                {
                    @include image-set('../../assets/img/components/weibo');   
                }
            }
        }
    }

    .icon-list.selector
    {
        .item
        {
            a
            {
                padding-top: 120px;
                height: 188px;
                
                @include mini-border(left, #E4E5E6, after);
            }
        }
        .item:first-child
        {
            a
            {
                &:after
                {
                    content: none;
                }
            }
        }
    }

</style>




