import Vue from 'vue';

// import { Button, Cell, Field, Switch, TabItem, TabContainerItem, TabContainer, Radio } from 'mint-ui-rem-dpr';

import Button from 'mint-ui-rem-dpr/lib/button';
import Cell from 'mint-ui-rem-dpr/lib/cell';
import Field from 'mint-ui-rem-dpr/lib/field';
import Switch from 'mint-ui-rem-dpr/lib/switch';
import TabItem from 'mint-ui-rem-dpr/lib/tab-item';
import TabContainerItem from 'mint-ui-rem-dpr/lib/tab-container-item';
import TabContainer from 'mint-ui-rem-dpr/lib/tab-container';
import Radio from 'mint-ui-rem-dpr/lib/radio';
import Navbar from 'mint-ui-rem-dpr/lib/navbar';

import 'mint-ui-rem-dpr/lib/button/style.css';
import 'mint-ui-rem-dpr/lib/Cell/style.css';
import 'mint-ui-rem-dpr/lib/Field/style.css';
import 'mint-ui-rem-dpr/lib/Switch/style.css';
import 'mint-ui-rem-dpr/lib/Tab-item/style.css';
import 'mint-ui-rem-dpr/lib/Tab-Container-Item/style.css';
import 'mint-ui-rem-dpr/lib/Tab-Container/style.css';
import 'mint-ui-rem-dpr/lib/Radio/style.css';
import 'mint-ui-rem-dpr/lib/navbar/style.css';

Vue.component(Button.name, Button);
Vue.component(Cell.name, Cell);
Vue.component(Field.name, Field);
Vue.component(Switch.name, Switch);
Vue.component(TabItem.name, TabItem);
Vue.component(TabContainerItem.name, TabContainerItem);
Vue.component(TabContainer.name, TabContainer);
Vue.component(Radio.name, Radio);
Vue.component(Navbar.name, Navbar);


