<template>
    <div class="infinite-scroll-wrapper" ref="wrapper"  >
        <div class="infinite-scroll">
            <slot></slot>
            <div class="bottom-placeholder"></div>
            <div class="bottom-loading">
                <cite v-if="hasMoreData">
                    <svg xmlns:svg="http://www.w3.org/2000/svg" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.0" width="64px" height="64px" viewBox="0 0 128 128" xml:space="preserve"><g><path d="M122.5 69.25H96.47a33.1 33.1 0 0 0 0-10.5h26.05a5.25 5.25 0 0 1 0 10.5z" fill="#757575"/><path d="M112.04 97.83L89.47 84.8a33.1 33.1 0 0 0 5.25-9.1l22.57 13.03a5.25 5.25 0 0 1-5.28 9.1z" fill="#d5d5d5"/><path d="M88.68 117.35L75.65 94.78a33.1 33.1 0 0 0 9.1-5.25l13.02 22.57a5.25 5.25 0 1 1-9.1 5.25z" fill="#d5d5d5"/><path d="M58.7 122.57V96.5a33.1 33.1 0 0 0 10.5 0v26.07a5.25 5.25 0 0 1-10.5 0z" fill="#d5d5d5"/><path d="M30.1 112.1l13.04-22.57a33.1 33.1 0 0 0 9.1 5.25L39.2 117.35a5.25 5.25 0 1 1-9.1-5.25z" fill="#d5d5d5"/><path d="M10.6 88.74L33.16 75.7a33.1 33.1 0 0 0 5.25 9.1L15.88 97.83a5.25 5.25 0 1 1-5.25-9.1z" fill="#d5d5d5"/><path d="M5.37 58.75h26.06a33.1 33.1 0 0 0 0 10.5H5.37a5.25 5.25 0 0 1 0-10.5z" fill="#c8c8c8"/><path d="M15.85 30.17L38.4 43.2a33.1 33.1 0 0 0-5.24 9.1L10.6 39.25a5.25 5.25 0 1 1 5.25-9.1z" fill="#bababa"/><path d="M39.2 10.65l13.03 22.57a33.1 33.1 0 0 0-9.1 5.25l-13-22.57a5.25 5.25 0 1 1 9.1-5.25z" fill="#acacac"/><path d="M69.2 5.43V31.5a33.1 33.1 0 0 0-10.5 0V5.42a5.25 5.25 0 1 1 10.5 0z" fill="#9e9e9e"/><path d="M97.77 15.9L84.75 38.47a33.1 33.1 0 0 0-9.1-5.25l13.03-22.57a5.25 5.25 0 1 1 9.1 5.25z" fill="#919191"/><path d="M117.3 39.26L94.7 52.3a33.1 33.1 0 0 0-5.25-9.1l22.57-13.03a5.25 5.25 0 0 1 5.25 9.1z" fill="#838383"/><animateTransform attributeName="transform" type="rotate" values="0 64 64;30 64 64;60 64 64;90 64 64;120 64 64;150 64 64;180 64 64;210 64 64;240 64 64;270 64 64;300 64 64;330 64 64" calcMode="discrete" dur="1080ms" repeatCount="indefinite"></animateTransform></g></svg>加载中，请稍等…
                </cite>
                <cite v-else class="no-data" >别滑了，没有更多了</cite>
            </div>
        </div>
    </div>
</template>

<script >
import BScroll from 'better-scroll';
export default {
    props: {
        /**
            * 1 滚动的时候会派发scroll事件，会截流。
            * 2 滚动的时候实时派发scroll事件，不会截流。
            * 3 除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
        */
        probeType: {
            type: Number,
            default: 3
        },
        /**
            * 点击列表是否派发click事件
        */
        click: {
            type: Boolean,
            default: true
        },
        /**
            * 是否开启横向滚动
        */
        scrollX: {
            type: Boolean,
            default: false
        },
        /**
            * 是否启用回弹动画效果
        */
        bounce: {
            type: Boolean,
            default: true
        },
        /**
            * 是否派发滚动开始之前事件
        */
        listenBeforeScrollStart: {
            type: Boolean,
            default: false
        },
        /**
            * 是否派发滚动开始事件
        */
        listenScrollStart: {
            type: Boolean,
            default: true
        },
        /**
            * 是否派发滚动事件
        */
        listenScroll: {
            type: Boolean,
            default: false
        },
        listenScrollEnd: {
            type: Boolean,
            default: true
        },
        /**
            * 列表的数据
        */
        data: {
            type: Array,
            default: null
        },
        /**
            * 是否派发滚动到底部的事件，用于上拉加载
        */
        pullup: {
            type: Boolean,
            default: true
        },
        /**
            * 是否派发顶部下拉的事件，用于下拉刷新
        */
        pulldown: {
            type: Boolean,
            default: false
        },
        /**
            * 是否派发列表滚动开始的事件
        */
        beforeScroll: {
            type: Boolean,
            default: false
        },
        /**
            * 当数据更新后，刷新scroll的延时。
        */
        refreshDelay: {
            type: Number,
            default: 20
        },
        hasMoreData: {
            type: Boolean,
            default: true
        }
    },
    data () {
        return {
            firstScrollTop: false
        };
    },
    mounted() {
        // 保证在DOM渲染完毕后初始化better-scroll
        setTimeout(() => {
            this._initScroll();
        }, 20);
    },
    methods: {
        _initScroll() {
            if (!this.$refs.wrapper) {
                return;
            }
            let _holderHeight = this.$refs.wrapper.children[0].clientHeight - this.$refs.wrapper.clientHeight;
            if( _holderHeight < 0 ) {
                this.firstScrollTop = true;
                document.querySelector('.bottom-placeholder').style.height = Math.abs( this.$flexible.convert( _holderHeight - 500 ) ) + 'px';
            }
            // better-scroll的初始化
            this.scroll = new BScroll(this.$refs.wrapper, {
                probeType: this.probeType,
                click: this.click,
                scrollX: this.scrollX,
                bounce: this.bounce
            });
            // 是否派发滚动到底部事件，用于上拉加载
            if (this.pullup) {
                this.scroll.on('scroll', () => {
                    // 滚动到底部
                    if (this.scroll.y <= (this.scroll.maxScrollY )) {
                        this.$emit('pullup');
                        if( this.scroll.maxScrollY < 0 ) {
                            document.querySelector('.bottom-loading').setAttribute('class', 'bottom-loading show');
                            document.querySelector('.bottom-placeholder').style.height = '0px';
                        }
                    }
                });
            }
            if( this.listenScrollEnd ) {
                this.scroll.on('scrollEnd', (pos) => {
                    if( this.firstScrollTop ) {
                        this.scroll.scrollTo(0, 0, 80);
                        this.firstScrollTop = false;
                    }
                });
            }
        },
        isable() {
            // 代理better-scroll的disable方法
            this.scroll && this.scroll.disable();
        },
        enable() {
            // 代理better-scroll的enable方法
            this.scroll && this.scroll.enable();
        },
        refresh() {
            // 代理better-scroll的refresh方法
            this.scroll && this.scroll.refresh();
        },
        scrollTo() {
            // 代理better-scroll的scrollTo方法
            this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
        },
        scrollToElement() {
            // 代理better-scroll的scrollToElement方法
            this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
        }
    },
    watch: {
        data () {
            setTimeout(() => {
                this.refresh();
            }, this.refreshDelay);
        }
    }
  };
</script>

<style lang="scss">
    .infinite-scroll-wrapper
    {
        position: relative;
        height: 100%;
        overflow: hidden;
        
    }
    .bottom-loading
    {
        height: 100px;
        line-height: 100px;
        text-align: center;
        color: #999;
        cite
        {
            position: relative;
            padding-left: 75px;
            display: none;

            svg
            {
                position: absolute;
                top: 50%;
                left: 0px;
                width: 60px;
                height: 60px;
                @include transform(translateY(-50%));
            }


            &.no-data
            {
                padding-left: 50px;
                &:before
                {
                    content: '';
                    position: absolute;
                    top: 50%;
                    left: 0px;
                    width: 40px;
                    height: 40px;
                    @include image-set('../assets/img/components/infinite-no-data');   
                    animation: none;
                    @include transform(translateY(-55%));
                }
            }
        }
        
        &.show
        {
            cite
            {
                display: inline-block;
            }
        }
    }
    .bottom-placeholder
    {
        position: relative;
        overflow: hidden;   
    }

    @keyframes infinite-loading
    {
        0%
        {
            @include transform( translateY(-50%) rotate( 0deg));
        }
        100%
        {
            @include transform( translateY(-50%) rotate( 360deg));
        }
    }
</style>


