import jsbridge from './jsbridge';
const hybrid = {
    installed: false,
    install: function( Vue ){
        Object.defineProperty(Vue.prototype, '$hybrid', {
            get () {
                return {
                    call(){
                        jsbridge.call.apply(jsbridge, arguments);
                    },
                    subEvent() {
                        jsbridge.subEvent.apply(jsbridge, arguments);
                    }
                };
            }
        });
    }
};
if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.use(hybrid);
}
export default hybrid;



