import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);

import jsbridge from '@/assets/js/jsbridge';

import store from '@/store';
import pathMap from './path-map';
import pathNativeMap from './path-native-map';

import routesBank from './bank';
import routesLife from './life';
import routesCredit from './credit';
import routesMy from './my';
import routesSelector from './selector';

// 测试
const testIndex = r => require.ensure([], () => r(require('@/pages/test-index')), 'test-module');
const infiniteScroll = r => require.ensure([], () => r(require('@/pages/infinite-scroll')), 'test-module');
const textMyIndex = r => require.ensure([], () => r(require('@/pages/bank/accounts/test-my-index')), 'test-module');
// 测试

const home = r => require.ensure([], () => r(require('@/pages/home/index')), 'home');
const shortcutSetting = r => require.ensure([], () => r(require('@/pages/home/shortcut-setting')), 'home');
const bank = r => require.ensure([], () => r(require('@/pages/bank/index')), 'home');
const life = r => require.ensure([], () => r(require('@/pages/life')), 'home');
const my = r => require.ensure([], () => r(require('@/pages/my/index')), 'home');
const messageCenter = r => require.ensure([], () => r(require('@/pages/home/message-center/index')), 'home');
const messagetDetail = r => require.ensure([], () => r(require('@/pages/home/message-center/detail')), 'home');

let routes = [
    {
        path: '/',
        name: 'testIndex',
        component: testIndex
    },
    {
        path: '/my-index',
        name: 'textMyIndex',
        component: textMyIndex
    },
    {
        path: '/infinite-scroll',
        name: 'infiniteScroll',
        component: infiniteScroll
    },
    {
        path: '/home',
        name: 'home',
        component: home
    },
    {
        path: '/home/shortcut-setting',
        name: 'shortcutSetting',
        component: shortcutSetting
    },
    {
        path: '/bank',
        name: 'bank',
        component: bank
    },
    {
        path: '/life',
        name: 'life',
        component: life
    },
    {
        path: '/my',
        name: 'my',
        component: my
    },
    // 消息中心
    {
        path: '/message/index',
        name: 'messageCenter',
        component: messageCenter,
        meta: {
            groupName: 'message-center'
        }
    },
    {
        path: '/message/detail',
        name: 'messagetDetail',
        component: messagetDetail,
        meta: {
            groupName: 'message-center'
        }
    }
];

routes = routes.concat(routesBank, routesLife, routesCredit, routesMy, routesSelector);

const router = new VueRouter({
    routes
});

const defaultNativeMenu = {
    left: {
        key: 'back',
        icon: 'back'
    }
};
// 全局路由控制
router.beforeEach((to, from, next) => {
    console.log('global beforeEach');
    let _newActivity = to.query.newActivity;
    let _toPath = to.path;
    let _toMenuNo = pathMap[_toPath];
    let _toMenu = store.state.menuAuth.menuMap[_toMenuNo];
    console.log(`to path: ${_toPath}; to menu no: ${_toMenuNo}; to menu: ${JSON.stringify( _toMenu )}`);

    // 权限判断
    if (_toMenu && _toMenu.MENU_ROLE != 2) {
        console.log(`Menu No ${_toMenu.MENU_NO} 权限不足。`);
        next(false);
    }
    // 注入菜单
    let _cacheMenu = _toMenuNo ? store.state.menuAuth.menuCacheMap[_toMenuNo] : null;
    console.log(`cache menu :${JSON.stringify(_cacheMenu)}`);
    if (_toMenuNo && !_cacheMenu) {
        _cacheMenu = store.state.menuAuth.menuList.filter((item) => { return item.SUPER_MENU == _toMenuNo });
        // 缓存
        store.dispatch('addMenuCacheMap', { menuNo: _toMenuNo, menuList: _cacheMenu });
    }
    let _tempMenu = Object.assign({}, _toMenu);
    _tempMenu.SUB_MENU_LIST = Object.assign([], _cacheMenu);
    to.meta.menuAuth = _tempMenu;
    if( _newActivity === undefined ) {
        next();
        return;
    }
    let _nativeKey = _toMenuNo || _toPath;
    let _nativeMenu = Object.assign({}, pathNativeMap[ _nativeKey ] || defaultNativeMenu );
    if( _nativeMenu && !_nativeMenu.title ) {
        _nativeMenu.title = _toMenu.MENU_NAME;
    }
    if( _nativeMenu && !_nativeMenu.url ) {
        _nativeMenu.url = to.path;
    }
    _nativeMenu.url = jsbridge.mixinURL(_nativeMenu.url);
    console.log(`menu no: ${_toMenuNo}, native menu: ${JSON.stringify(_nativeMenu)}`);
    console.log('jsbridge');
    console.log(jsbridge);
    jsbridge.call({
        method: 'createPage',
        params: _nativeMenu
    });
    next(false);
    return;
});

router.afterEach((to, from) => {
    // console.log('global afterEach');
});

export default router;



