const state = {
    search: {
        payeeName: ''
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateCardQuerySearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearCardQuerySearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            payeeName: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



