<template>
    <div>
        <div class="transfer-money">
            <span class="transfer-money-span">{{title}}</span>
            <div class="mint-cell mint-field is-nolabel">
                <div class="mint-cell-wrapper">
                    <div class="mint-cell-value">
                        <label class="transfer-money-label">￥</label>
                        <input id="m-input" class="mint-field-core" :class=" {'mint-field-core-color': field_err }" type="text" :placeholder="placeholder" :value="currentValue" :totalMoney="totalMoney"
                            @input="handleInput" @blur="handleBlur" @focus="handleFocus">
                        <hr class="transfer-hr" />
                        <div class="mint-field-clear mint-field-clear-id" v-if="field_clear" @click="fclear">
                            <i class="mintui mintui-field-error"></i>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <span v-if='field_err' class="svalidate" :class=" {'svalidate-two': twoValidate == 'show'}" id="svalidate">{{validate}}</span>
    </div>
</template>

<script>
    export default {
        props: {
            title: {
                type: String,
                default: '转账金额'
            },
            totalMoney: '',
            placeholder: {
                type: String,
                default: '请输入转账金额'
            },
            validate: {
                type: String,
                default: '转账金额不能大于原始金额'
            },
            twoValidate: '',
            value: {}
        },
        data() {
            return {
                field_err: false,
                field_clear: false,
                money: '',
                currentValue: this.value
            };
        },
        watch: {
            value (val){
                this.currentValue = val;
            },
            currentValue (val){
                this.handleFocus();
                this.changeMoney();
                this.$emit('input', val);
            },
            totalMoney (){
                this.changeMoney();
            }
        },
        methods: {
            handleInput (evt){
                this.currentValue = evt.target.value;
            },
            fclear() {
                this.currentValue = '';
                this.field_err = false;
            },
            changeMoney (){
                var totalMoney = this.totalMoney;
                if (Number(this.currentValue) > Number(totalMoney)) {
                    this.field_err = true;
                } else {
                    this.field_err = false;
                }
            },
            handleFocus (){
                if (this.currentValue != '') {
                    this.field_clear = true;
                } else {
                    this.field_clear = false;
                }
                this.$emit('focus');
            },
            handleBlur (){
                var self = this;
                setTimeout(function(){
                    self.field_clear = false;
                }, 0);
                this.$emit('blur');
            }
        }
    };

</script>

<style lang="scss" scoped>
    .transfer-money {
        background: #fff;
        width: 750px;
        height: 230px;
        .transfer-money-span {
            display: block;
            padding: 40px 0px 24px 50px;
            font-family: PingFangSC-Regular;
            font-size: 16px;
            color: #272727;
            letter-spacing: 0;
        }
        .transfer-money-label {
            font-family: PingFangSC-Regular;
            font-size: 20px;
            color: #272727;
            letter-spacing: 0;
            padding-right: 20px;
        }
        .transfer-hr {
            border: 0.5px solid #E4E5E6;
            position: absolute;
            left: 100px;
            top: 95px;
            right: 50px;
        }
    }

    .mint-field-core-color {
        color: #FF511A;
    }

    .svalidate {
        margin: 20px 0px 0px 44px;
        opacity: 0.8;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #FF511A;
        line-height: 22px;
    }

    .svalidate-two {
        position: absolute;
    }
</style>


