const state = {
    detail: {
        status: 2,
        period: '',
        order: 2,
        account: ''
    },
    value: {
        status: '可购买',
        period: '不限',
        order: '发行日期'
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateLargeDepositDeail: ({ commit }, data) => {
        commit('UPDATE_DETAIL', data);
    },
    clearLargeDepositDetail: ({ commit }, data ) => {
        commit('CLEAR_DETAIL');
    },
    updateLargeDepositValue: ({ commit }, data) => {
        commit('UPDATE_VALUE', data);
    },
    clearLargeDepositValue: ({ commit }, data ) => {
        commit('CLEAR_VALUE');
    }
};

// mutations
const mutations = {
    UPDATE_DETAIL (state, data) {
        state.detail = data;
    },
    CLEAR_DETAIL (state, data) {
        state.detail = {
            status: 2,
            period: '',
            order: 2,
            account: ''
        };
    },
    UPDATE_VALUE (state, data) {
        state.value = data;
    },
    CLEAR_VALUE (state, data) {
        state.value = {
            status: '可购买',
            period: '不限',
            order: '发行日期'
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



