
<template>
    <div class="selector-inline selector-share" :class="{ show : currentShow }"   >
        <div class="mask" @click=" close()  "></div>
        <div class="content">
            <div class="hd">
                <a href="javascript:;" class="btn-cancel" @click=" close() ">取消</a>
                <h2>分享</h2>
            </div>
            <div class="bd">
                <share-panel @click="share" type="selector" ></share-panel>
            </div>
        </div>
    </div>
</template>

<script>

import preventPageScroll from 'wc-utils/prevent-page-scroll';

export default {
    data() {
        return {
            currentShow: false
        };
    },
    created() {
    },
    computed: {
    },
    components: {
    },
    methods: {
        share () {
            this.currentShow = false;
            this.resolve('success');
        },
        close () {
            preventPageScroll.recover();
            this.currentShow = false;
            this.reject();
        },
        show ( contentHtml ) {
            preventPageScroll.prevent();
            this.currentShow = true;
            this.promise = new Promise((resolve, reject) => {
                this.resolve = resolve;
                this.reject = reject;
            });
            return this.promise;
        }
    }
};
</script>

<style lang="scss" scoped>

    $item-height: 120px;

    
</style>



