const state = {
    search: {
        paymentAccountList: [],
        checked: {},
        patternName: '实时到账',
        TRAN_TYPE: '0', // 转账方式 0-实时 1-次日
        COR_NAME: '', // 收款户名
        PAYEE_ACCOUNT: '', // 收款账号
        COR_BANKNUMBER: '', // 收款银行编号
        COR_BANKNAME: '', // 收款人银行名
        PAYER_ACCOUNT: '', // 付款账户
        CURRENCY: '0', // 付款币种 0-人民币
        AMOUNT: '', // 转账金额
        FEE: '', // 转账手续费
        PAY_PWD: '',  // 交易密码
        SENDMENO: '', // 附言
        payeeBankName: '',
        bankCity: '',
        bankBranch: ''
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateAgriculturalCreditTransferSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearAgriculturalCreditTransferSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            paymentAccountList: [],
            checked: {},
            patternName: '实时到账',
            TRAN_TYPE: '0',
            COR_NAME: '',
            PAYEE_ACCOUNT: '',
            COR_BANKNUMBER: '',
            COR_BANKNAME: '',
            PAYER_ACCOUNT: '',
            CURRENCY: '0',
            AMOUNT: '',
            FEE: '',
            PAY_PWD: '',
            SENDMENO: '',
            payeeBankName: '',
            bankCity: '',
            bankBranch: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



