const state = {
    search: {
        account: '',
        money: '',
        balance: '',
        payeeName: '',
        payeeAccount: '',
        payeeBankName: ''
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateCardIntoSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearCardIntoSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            account: '',
            money: '',
            balance: '',
            payeeName: '',
            payeeAccount: '',
            payeeBankName: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



