<template>
    <div class="bank-card">
        <ul class="card-list">
            <li class="item" :class="{'item-loss': item.STATUS && item.STATUS != 1}" v-for="( item, index ) in bankCardList">
                <a href="javascript:;" @click="bankCardClick( item )" :class="{'credit': cardType === 'credit'}">
                    <div class="attr">
                        <i class="logo"></i>
                        <div class="bank">
                            <span class="name">{{ item.FJGM || item.ACCT_JGMC}}</span>
                            <span v-if="cardType === 'credit'" class="type credit-type">{{item.ACCT_TYPE}}</span>
                            <span class="type" v-else>{{ item.ACCT_TYPE}}</span>
                        </div>
                    </div>
                    <div class="number">
                        <span>{{(item.CARD || item.ACCOUNT) | formatAccount}}</span>
                    </div>
                    <div v-show="item.ALIAS" class="alias">
                        <span class="line line-left"></span>
                        <span class="msg">{{ item.ALIAS }}</span>
                        <span class="line line-right"></span>
                    </div>
                    <div v-if="item.ZF_FLAG == 1" class="default">
                        <div class="msg">主账户</div>
                    </div>
                </a>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: {
        cardType: '',
        bankCardList: {
            type: Array,
            default: []
        }
    },
    data() {
        return {};
    },
    computed: {
        // _bankCardList() {
        //     console.log(this.bankCardList);
        //     return this.bankCardList.sort(function (x, y) {
        //             if (x.ACCOUNT < y.ACCOUNT) {
        //                 return 1;
        //             }
        //             if (x.ACCOUNT > y.ACCOUNT) {
        //                 return -1;
        //             }
        //             return 0;
        //         });
        // }
    },
    created() {
    },
    methods: {
        bankCardClick ( item ) {
            this.$emit('bank-card-click', item);
        }
    }
};

</script>

<style lang="scss" scoped>

    .bank-card
    {
        .card-list
        {
            .item
            {
                a
                {
                    position: relative;
                    display: block;
                    padding: 30px 30px 0;
                    height: 238px;
                    @include border-radius(8px);
                    @include image-set('../../assets/img/bank/card/bank');
                }
                .credit
                {
                    @include image-set('../../assets/img/bank/card/credit');
                }

                .attr
                {
                    position: relative;
                    padding-left: 56px;
                    .logo
                    {
                        position: absolute;
                        display: block;
                        top: 0;
                        left: 0;
                        width: 36px;
                        height: 36px;
                        @include image-set('../../assets/img/bank/card/logo');
                    }

                    .bank
                    {
                        position: relative;
                        display: inline-block;
                        height: 42px;
                        font-size: 15px;
                        color: #333333;
                        .name{
                            display: inline-block;
                            max-width: 440px;
                            @include ellipsis();
                        }
                    }
                    .type
                    {
                        position: absolute;
                        top: 4px;
                        right: -120px;
                        width: 100px;
                        height: 32px;
                        font-size: 12px;
                        color: #B5B6BE;
                        text-align: center;
                        line-height: 32px;
                        @include image-set('../../assets/img/bank/card/label');
                    }
                    .credit-type
                    {
                        color: #e5c375;
                    }
                }
                .number
                {
                    margin-top: 32px;
                    text-align: center;
                    font-size: 15px;
                    color: #272727;
                    font-weight: normal;
                    letter-spacing: 2.3px;
                    font-family: Farrington7B_Qiqi, "Helvetica Neue", Helvetica, STHeitiTC, sans-serif;
                }
                .alias{
                    position: absolute;
                    padding: 0 80px;
                    left: 50%;
                    bottom: 30px;
                    transform: translateX(-50%);
                    width: 70%;
                    height: 36px;
                    line-height: 36px;
                    text-align: center;
                    overflow: hidden;
                    .msg{
                        display: inline-block;
                        max-width: 300px;
                        padding: 0 18px 0 26px;
                        color: #989a9c;
                        font-size: 12px;
                        @include ellipsis();
                    }
                    .line{
                        position: absolute;
                        display: inline-block;
                        top: 16px;
                        width: 8px;
                        height: 8px;
                        background-color: rgba(166, 168, 176, 0.5);
                        border-radius: 50%;
                        &:after{
                            content: '';
                            position: absolute;
                            display: block;
                            top: 4px;
                            width: 400px;
                            border-top: 1px solid rgba(166, 168, 176, 0.5);
                        }
                    }
                    .line-left:after{
                        right: 8px;
                    }
                    .line-right:after{
                        left: 8px;
                    }
                }
                .default
                {
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 84px;
                    height: 84px;
                    @include image-set('../../assets/img/bank/card/default');
                    .msg
                    {
                        position: absolute;
                        top: 24%;
                        left: 12%;
                        width: 100px;
                        color: #fff;
                        font-size: 10px;
                        transform: rotate(45deg) scale(0.8);
                    }
                }
            }
            .item + .item
            {
                margin-top: 30px;
            }
            .item-loss
            {
                a
                {
                    @include image-set('../../assets/img/bank/card/not-active');
                }
                .attr
                {
                    .logo
                    {
                        @include image-set('../../assets/img/bank/card/not-active-logo');
                    }
                    .bank{
                        color: #999999;
                    }
                }
                .number
                {
                    color: #999999;
                }
                .alias
                {
                    .alias-msg
                    {
                        color: #999999;
                    }
                }
            }
        }
    }
   
</style>


