const state = {
    search: {
        USER_NAME: '', // 姓名
        IDCARD: '', // 身份证
        MOBILE: '', // 手机号
        ACCT_TYPE: '', // 账户类型
        PAYER_ACCOUNT: '', // 账号
        ACCTSOURCE: '', // 账户来源
        BINDACCTTYPE: '', // 绑定账户类型
        PAY_PWD: '', // 支付密码
        checkbox: ''
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateAccountOpeningSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearAccountOpeningSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            USER_NAME: '',
            IDCARD: '',
            MOBILE: '',
            ACCT_TYPE: '',
            PAYER_ACCOUNT: '',
            ACCTSOURCE: '',
            BINDACCTTYPE: '',
            PAY_PWD: '',
            checkbox: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



