const state = {
    search: {
        paymentAccountList: [],
        checked: {},
        patternName: '普通到账',
        TRAN_TYPE: '1', // 转账方式
        PAYEE_NAME: '', // 收款户名
        PAYEE_ACCOUNT: '', // 收款账号
        PAYEE_BANK_NO: '', // 收款银行编号
        PAYMENT_ACCOUNT: '', // 付款账户
        TRAN_CURRENCY: '0', // 付款币种 0-人民币
        TRAN_AMOUNT: '', // 转账金额
        TRAN_FEE: '', // 转账手续费
        PAY_PWD: '',  // 交易密码
        SENDMENO: '', // 附言
        payeeBankName: '',
        bankName: '',
        bankCity: '',
        bankBranch: ''
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateExternalTransferSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearExternalTransferSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            paymentAccountList: [],
            checked: {},
            patternName: '普通到账',
            TRAN_TYPE: '1',
            PAYEE_NAME: '',
            PAYEE_ACCOUNT: '',
            PAYEE_BANK_NO: '',
            PAYMENT_ACCOUNT: '',
            TRAN_CURRENCY: '0',
            TRAN_AMOUNT: '',
            TRAN_FEE: '',
            PAY_PWD: '',
            SENDMENO: '',
            payeeBankName: '',
            bankName: '',
            bankCity: '',
            bankBranch: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



