<template>
    <div class="fund-card" :class="{ 'detail' : cardType == '2', 'payee-detail' : cardType == 'myPayee'  }" @click="cardQueryClick">
        <h3>
            <cite>{{data.name}}</cite>
            <span class="card-type" v-if="cardType != 'myPayee'">储蓄卡</span>
            <span class="transfer-type" :class="[ bank.TRANSFER_TYPE ]" v-else >农信银</span>
        </h3>
        <p class="account">{{data.account}}</p>
        <p class="bankname">{{data.bankName}}</p>
        <span class="logo" :class="[ data.bankCode ]"></span>
        
    </div>
</template>

<script>
export default {
    props: {
        data: null,
        cardType: ''
    },
    data() {
        return {};
    },
    methods: {
        cardQueryClick () {
            this.$emit('card-query-click');
        }
    }
};

</script>

<style lang="scss" scoped>
.fund-card 
{
    position: relative;
    padding: 40px 0px 0px 50px;
    height: 216px;
    width: 710px;
    background-repeat: no-repeat;
    background-size: 100%;

    @include image-set('../../assets/img/bank/card/card-bg');

    &.detail
    {
        padding-top: 30px;
        width: 750px;
        @include image-set('../../assets/img/bank/card/card-detail-bg');

        .account
        {
            margin-top: 30px;
        }
    }

    h3
    {
        line-height: 42px;
        font-size: 15px;
        color: #333333;
        @include clearfix();

        cite,span
        {
            float: left;
        }
        .card-type
        {
            margin: 4px 0px 0px 15px;
            width: 100px;
            height: 32px;
            line-height: 32px;
            text-align: center;
            font-size: 12px;
            color: #B5B6BE;

            @include image-set('../../assets/img/bank/card/card-type');
        }
    }

    .account
    {
        margin-top: 12px;
        line-height: 36px;
        font-size: 14px;
        color: #444857;
        letter-spacing: 2px;

        @include account-font();
    }

    .bankname
    {
        margin-top: 20px;
        line-height: 36px;
        font-size: 13px;
        color: #333333;
    }

    .logo
    {
        position: absolute;
        top: 0px;
        right: 0px;
        z-index: -1;
        height: 100%;
        width: 100%;
    }
}
</style>


