const MODULE_NAME = 'selector';
// 选择收款人
const selectorPayee = r => require.ensure([], () => r(require('@/pages/selector/payee')), 'selector');
const selectorBank = r => require.ensure([], () => r(require('@/pages/selector/bank')), 'selector');
const selectorBankCity = r => require.ensure([], () => r(require('@/pages/selector/bank-city')), 'selector');
const selectorBankBranch = r => require.ensure([], () => r(require('@/pages/selector/bank-branch')), 'selector');
const selectorOtherCreditCard = r => require.ensure([], () => r(require('@/pages/selector/other-credit-card')), 'selector');
const selectorDemo = r => require.ensure([], () => r(require('@/pages/selector/demo')), 'selector');

export default [
    {
        path: '/selector/payee',
        name: 'selectorPayee',
        component: selectorPayee,
        meta: {
            groupName: 'selector'
        }
    },
    {
        path: '/selector/bank',
        name: 'selectorBank',
        component: selectorBank,
        meta: {
            groupName: 'selector'
        }
    },
    {
        path: '/selector/bank-city',
        name: 'selectorBankCity',
        component: selectorBankCity,
        meta: {
            groupName: 'selector'
        }
    },
    {
        path: '/selector/bank-branch',
        name: 'selectorBankBranch',
        component: selectorBankBranch,
        meta: {
            groupName: 'selector'
        }
    },
    {
        path: '/selector/other-credit-card',
        name: 'selectorOtherCreditCard',
        component: selectorOtherCreditCard,
        meta: {
            groupName: 'selector'
        }
    },
    {
        path: '/selector/demo',
        name: 'selectorDemo',
        component: selectorDemo
    }
];


